// Backdrop-mobile
.backdrop-mobile {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--bgc-modal);
    // cursor: not-allowed;
    visibility: visible;
    opacity: 1;
    transition: 
    transform var(--default-transition),
    visibility var(--default-transition),
    opacity var(--default-transition);
    z-index: 2;
    &__container {
        width: 100%;
        height: 100%;
    }
}